import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';

function TypeDetail({ match }) {

    const [type, setType] = useState({})
    const [posts, setPosts] = useState([])
    const id = match.params.id

    useEffect(() => {
        axios({
            method: "GET",
            url: `http://127.0.0.1:8000/api/type/${id}/`
        }).then(response => {
            setType(response.data)
            setPosts(response.data.posts)
        })
    }, [id])

    return (
        <div>
            Type with id {type.id}
            <p>Type: <strong>{type.name}</strong></p>
            <hr />
            <div className="row">
                {posts.map(p => (
                    <div className="col-md-12" key={p.id}>
                        <h4>{p.title}</h4>
                        <p>{p.content}</p>
                        <p>{p.id}</p>
                        <Link to={{ pathname: `/posts/${p.id}`, fromDashboard: false }}>Детали</Link>
                    </div>
                ))}
            </div>
            <div class="d-flex flex-column flex-shrink-0 p-3 text-white bg-dark" style="width: 280px;">
                <a href="/" class="d-flex align-items-center mb-3 mb-md-0 me-md-auto text-white text-decoration-none">
                    <span class="fs-4">Sidebar</span>
                </a>
                <hr />
                <ul class="nav nav-pills flex-column mb-auto">
                    <li class="nav-item">
                        <a href="#" class="nav-link active">
                            Home
        </a>
                    </li>
                    <li>
                        <a href="#" class="nav-link text-white">
                            Dashboard
        </a>
                    </li>
                    <li>
                        <a href="#" class="nav-link text-white">
                            Orders
        </a>
                    </li>
                    <li>
                        <a href="#" class="nav-link text-white">
                            Products
        </a>
                    </li>
                    <li>
                        <a href="#" class="nav-link text-white">
                            Customers
        </a>
                    </li>
                </ul>
            </div>
        </div>
    )
}

export default TypeDetail